.itemlist{
    list-style: none;
    display: flex;
    flex-direction: column;
    margin-top:0px;
    margin-left: -40px;
}
.ashCanvas{
    width: 100%;
    height:50vh; 
    background-color: lightslategray;
}
.whiteCanvas{
    width: 100%;
    height:50vh; 
    background-color: white;
}
.itemText{
    float:right;
    max-width: 55vw;
    margin-top: 2%;
    margin-right: 5%;
    text-align: left;
}
.itemTitle{
    font-size: 36px;
}

.itemDescription{
    font-size: 20px;
}
.viewingLinkContainer{
    float:right;
    width:100px;
    height: 50px;
    margin-top:50px;
    margin-bottom: 50px;
    margin-right: 10%;


}
.viewingLinkContainer :hover{
    background-color: rgb(56, 146, 56);
}
.viewingLink{
    margin-top: 1%;
    margin-bottom: 70px;
    margin-left: 50%;
    font-size: 30px;
    background-color: green;
    text-decoration: none;
    color: black;
    border: solid;
    border-radius: 5pt;
    padding: 5% 10%;
}
.profile{
    float:right;
    margin-top: 15%;
    margin-right: 5%;
    text-align: center;
    width:45vw;
}
.profileBackground{

    background-color: lightslategray;
}
.profilepic{
    float:left;
    margin-left: 5%;
    margin-top: 10%;
    height: 70vh;
}
.profileTitle{
    font-size: 36px;
}
.profileDescription{
    font-size: 22px;
}